body {
  max-width: 800px;
  margin: 0 auto;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

p,
h1,
h2,
h3 {
  line-height: 1.4;
}
#root > :first-child {
  margin-top: 0;
}
main {
  padding: 0 2em 50px;
  margin: 2em 0;
}
main > h1:first-child {
  margin-bottom: 2em;
}
h1,
h2,
h3 {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  margin-bottom: 0.5em;
}
p {
  margin-top: 0.5em;
}
h1 {
  border-bottom: solid #e5cb2c 4px;
  font-size: 1.5em;
}
h2 {
  font-size: 1.2em;
}
h3 {
  font-size: 1.1em;
  color: #2c2c86;
}
.compassLayout {
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-end;
}
.compassLayout div:first-child {
  flex: 1 1 400px;
}
.compassLayout div:last-child {
  flex: 1 2 400px;
  padding-right: 1.5em;
}
button.themed {
  background-color: #e5cb2c;
  height: 2em;
  border-radius: 1em;
  border-color: thin solid #333;
  font-size: 1em;
  font-weight: bold;
}
.resultItem {
  border-left: thick solid dimgray;
  padding-left: 1em;
}
label, legend {
  font-weight: bold;
}
fieldset label {
  font-weight: normal;
}

.pageNavigation {
	border: none;
	background-color: transparent;
	position: fixed;
	bottom: 1em;
}
.pageNavigation[disabled] {
	display: none;
}
.pageNavigation span {
	color: white;
	font-weight: bold;
	display: inline-block;
	background-color: #e5cb2c;
	padding: 5px 10px;
	height: 26px;
	box-sizing: border-box;
	vertical-align: top;
	width: 7em;
}
.prevPage {
	right: 10em;
}
.prevPage::before {
	content: '';
	display: inline-block;
	width: 0;
	height: 0;
	border-right: 9px solid #e5cb2c;
	border-top: 13px solid transparent;
	border-bottom: 13px solid transparent;
}
.nextPage {
	right: 2em;
}
.nextPage:after {
	content: '';
	display: inline-block;
	width: 0;
	height: 0;
	border-left: 9px solid #e5cb2c;
	border-top: 13px solid transparent;
	border-bottom: 13px solid transparent;
}
textarea {
	height: 8em;
	font-size: 1em;
	width: 100%;
}
.ponderLayout {
	display: flex;
}
.ponderLayout section {
	flex: 1 1 50%;
}
.ponderLayout section:first-child {
	padding-right: 1em;
}
.ponderLayout section:last-child {
	background-color: rgb(250,246,217);
	padding: 1em;
}
.compass { /* a button element */
	width: 380px;
	height: 380px;
	background-image: url(/study/static/media/Compass.96939107.svg);
	background-color: transparent;
	background-size: contain;
	background-repeat: no-repeat;
	border: none;
	padding: 0;
	margin: 0;
	display: inline-block;
	position: relative;
	cursor: pointer;
}
.compass:disabled {
	cursor: default;
}

.compassDial {
	display: block;
	position: absolute;
	left: 0;
	top: 0;
	width: 380px;
	height: 380px;
	background-image: url(/study/static/media/CompassNeedle.75e77d07.svg);
	background-size: contain;
	background-repeat: no-repeat;
	background-position-x: 50%;
}

.isSpinning {
	-webkit-animation-name: cssAnimation;
	        animation-name: cssAnimation;
	-webkit-animation-duration: 4s;
	        animation-duration: 4s;
	-webkit-animation-timing-function: ease-out;
	        animation-timing-function: ease-out;
	-webkit-animation-delay: 0s;
	        animation-delay: 0s;
	-webkit-animation-iteration-count: 1;
	        animation-iteration-count: 1;
	-webkit-animation-direction: normal;
	        animation-direction: normal;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
}

@-webkit-keyframes cssAnimation {
	from {
		-webkit-transform: rotate(0turn);
		        transform: rotate(0turn);
	}
}

@keyframes cssAnimation {
	from {
		-webkit-transform: rotate(0turn);
		        transform: rotate(0turn);
	}
}

@-webkit-keyframes saveThis {
	to {
		-webkit-transform: rotate(6.3turn);
		        transform: rotate(6.3turn);
	}
}

@keyframes saveThis {
	to {
		-webkit-transform: rotate(6.3turn);
		        transform: rotate(6.3turn);
	}
}

.likertScale {
	margin: 1em 0;
}
.likertBand {
  display: flex;
  padding-top: 1em;
}
.likertResponse {
  flex: 1 1 5em;
  text-align: center;
  position: relative;
}
.likertLine {
  display: inline-block;
  width: 50%;
  vertical-align: top;
  margin-top: 0.5em;
  border-top: 3px solid dimgray;
}
.likertResponse:first-child .likertLine:first-child {
  visibility: hidden;
}
.likertResponse:last-child>.likertLine:nth-child(2) {
  visibility: hidden;
}
.likertIndicator {
  display: inline-block;
  width: 1em;
  height: 1em;
  border-radius: 0.5em;
  border: thin solid #870230;
  background-color: #ffed79;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  top: 0;
  box-sizing: border-box;
}
.likertResponse:hover .likertIndicator {
  background-color: white;
}
.likertResponse>input:checked+.likertIndicator {
  border-width: 3px;
  background-color: #870230;
}
.visuallyHidden { 
  position: absolute; 
  overflow: hidden; 
  clip: rect(0 0 0 0); 
  height: 1px; width: 1px; 
  margin: -1px; padding: 0; border: 0; 
}
.likertAnswer {
  display: none;
}
@media only print {
  .likertAnswer {
    display: inline-block;
  }
  .likertResponse>input:checked+.likertIndicator {
    background-color: black !important;
  }
}

